
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import VScroll from "../../pull-refresh";
import scroll from "../../scroll";

import { mapState, mapGetters } from "vuex";
export default {
directives: {
TransferDom
},
components: {
Group,
Cell,
Popup,
Checker,
CheckerItem,
Datetime,
Selector,

XButton,
scroll

},
computed: {
...mapGetters([
"helpPopup",
"helpsList",
"helpsListTotal",
"helpsBranch",
"branchList",
"helpsBranchSelect",
"helpScrollTop"
]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: "刷新完成"
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
created() {






},
mounted() {






},
activated() {
if (this.$route.query.status === "dynamic") {
this.$store.dispatch("getHelpsList");
} else if (this.$route.query.status === "statistics") {
this.$store.dispatch("getHelpsBranch", {
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate
});
this.$store.dispatch("getBranchList");
}






},
beforeDestroy() {

},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "刷新完成",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "加载更多",
pullUpLoadNoMoreTxt: "没有更多数据了",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
page: 1,
num: 10,



helps: [],
branchs: [],
isShow: false,
selectedDate: { key: 1, value: "本月" },
dateClass: [
{
key: 1,
value: "本月"
},
{
key: 2,
value: "本季度"
},
{
key: 3,
value: "本年"
}
],
startDate: "",
endDate: "",
ajaxData: [],
selectedData: "",
searchText: ""
};
},
watch: {
helpPopup: function(val) {
this.isShow = val;
},
startDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
endDate: function(val) {
if (val) {
this.selectedDate = {};
}
},
selectedDate: function(val) {
if (Object.keys(val).length) {
this.startDate = "";
this.endDate = "";
}
},
helpsList: function(val) {
this.helps = val;
},
helpsBranch: function(val) {
this.branchs = val;
},
branchList: function(val) {
this.ajaxData = val;
},
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
},
















$route(to, from) {
if (to.query.status === "dynamic" && to.path == "/home/help") {
if (this.helpsList.length == 0) {
this.$store.dispatch("getHelpsList");
}



} else if (to.query.status === "statistics" && to.path == "/home/help") {
this.$store.dispatch("getHelpsBranch");
this.$store.dispatch("getBranchList");

}
},
helpsBranchSelect(val) {
this.branchs = val;
}
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}

this.$store.dispatch("getHelpsList");
this.$refs.scroll.forceUpdate();
this.page = 1;
}, 2000);
},
onPullingUp() {
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.page++;
this.$store.dispatch("getHelpsList", { page: this.page }).then(() => {
if (this.helps.length >= this.helpsListTotal) {
this.$refs.scroll.forceUpdate();
}
});
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
},





























hidePopup() {
this.$store.dispatch("updateHelpPopup", {
helpPopup: false
});
},
setToday(value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = "0" + cmonth;
if (day < 10) day = "0" + day;
this.startDate = now.getFullYear() + "-" + cmonth + "-" + day;
},
searchData() {
this.searchText = searchText;
},
choosedData() {
if (!this.selectedDate && !this.startDate && !this.endDate) {
this.$vux.toast.text("请选择时间", "middle");
} else if (this.startDate && !this.endDate) {
this.$vux.toast.text("请选择结束时间", "middle");
} else if (!this.startDate && this.endDate) {
this.$vux.toast.text("请选择开始时间", "middle");
} else {
this.$store.dispatch("updateHelpPopup", {
helpPopup: false
});

this.$store.dispatch("getHelpBranchSelect", {
time: this.selectedDate.key,
start: this.startDate,
end: this.endDate,
department_id: this.selectedData
});

this.selectedData = "";

if (Object.keys(this.selectedDate).length) {
window.sessionStorage.removeItem("help_start");
window.sessionStorage.removeItem("help_end");
window.sessionStorage.setItem("help_time", this.selectedDate.key);
} else if (this.startDate && this.endDate) {
window.sessionStorage.removeItem("help_time");
window.sessionStorage.setItem("help_start", this.startDate);
window.sessionStorage.setItem("help_end", this.endDate);
}
}
}
}
};



